<template>
    <div :class="{ threeBg: true, leftAnimate: scrollNumForShow > scrollTo }">
        <div class="col-lg-4 col-md-4 col-sm-4 col-xs-12" v-for="threeBg in threeBgs" :key="threeBg.key">
            <div>
                <p>{{threeBg.word_1}}</p>
                <p>{{threeBg.word_2}}</p>
                <p class="more"><a href="javascript:" @click="pageInnerUrl(3)">äº†è§£æ›´å¤š</a></p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        scrollNumForShow: {
            type: Number,
            required: true
        }
    },
    data: function() {
        return {
            threeBgs: [
                {key: 0, word_1: 'ä¸€ç«™å¼æœåŠ¡', word_2: 'ä¸“ä¸šåŒ–æœåŠ¡å›¢é˜Ÿï¼Œæ ‡å‡†åŒ–å·¥è‰ºæµç¨‹ï¼Œè§„èŒƒåŒ–å–·æ¶‚è®¾å¤‡'},
                {key: 1, word_1: 'é¡¶å°–è¡Œä¸šæŠ€æœ¯', word_2: 'çº³ç±³æŠ€æœ¯åˆ¶æˆ5nmçººé”¤å½¢å¤åˆTiO2æ™¶ä½“ï¼Œæ›´ç¨³å®šï¼Œæ›´é«˜æ•ˆ'},
                {key: 2, word_1: 'è¦†ç›–å…¨å›½æœåŠ¡ç½‘ç‚¹', word_2: 'æ€»éƒ¨ä½äºŽåŒ—äº¬ï¼Œè¥é”€åŠæœåŠ¡ç½‘ç»œè¦†ç›–20å¤šä¸ªä¸€äºŒçº¿åŸŽå¸‚'}
            ],
            scrollTo: 0
        }
    },
    mounted: function() {
        var pageIndex = parseInt(sessionStorage.getItem("pageIndex"))
        if(pageIndex === 0) {
            var self = this;
            var scrollCacheFn = setInterval(function() {
                var scrollCache = 
                    document.getElementsByClassName('pageTop')[0].offsetHeight + 
                    document.getElementsByClassName('banner')[0].offsetHeight + 
                    document.getElementsByClassName('showSpace')[0].offsetHeight + 
                    document.getElementsByClassName('threeBg')[0].offsetTop - 
                    window.innerHeight
                if(self.scrollTo === scrollCache) {
                    clearInterval(scrollCacheFn)
                } else {
                    self.scrollTo = scrollCache
                }
            }, 1000)
            this.scrollCacheFn;
        }
    },
    methods: {
        pageInnerUrl: function(index) {
            this.$emit('pageInnerUrl', index)
            document.documentElement.scrollTop = 0
            sessionStorage.setItem("pageIndex", index); 
        }
    }
}
</script>

<style lang="stylus" scoped>
    
</style>



