<template>
    <div :class="{ showThree: true, upAnimate: scrollNumForShow > scrollTo }">
        <div v-for="showThree in showThrees" :key="showThree.id">
            <p><i class="iconfont" v-html="showThree.icon"></i></p>
            <p>{{showThree.word_1}}</p>
            <p v-html="showThree.word_2"></p>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        scrollNumForShow: {
            type: Number,
            required: true
        }
    },
    data: function() {
        return {
            showThrees: [
                { id: 0, icon: '&#xe63c;', word_1: 'åˆçº§æŠ€å¸ˆ', word_2: '<span>â— 3ä¸ªæœˆè€ƒæ ¸</span> <span>â— 0é—®é¢˜æ–½å·¥3000ãŽ¡</span> <span>â— è½¦å†…æ²»ç†</span> <span>â— å®¤å†…æ²»ç†</span>' },
                { id: 1, icon: '&#xe63a;', word_1: 'ä¸­çº§æŠ€å¸ˆ', word_2: '<span>â— 6ä¸ªæœˆè€ƒæ ¸</span> <span>â— 0é—®é¢˜æ–½å·¥6000ãŽ¡</span> <span>â— è½¦å†…æ²»ç†</span> <span>â— å®¤å†…æ²»ç†</span> <span>â— æ£€æµ‹&é‡æˆ¿</span>' },
                { id: 2, icon: '&#xe63b;', word_1: 'é«˜çº§æŠ€å¸ˆ', word_2: '<span>â— 12ä¸ªæœˆè€ƒæ ¸</span> <span>â— 0é—®é¢˜æ–½å·¥10000ãŽ¡</span> <span>â— è½¦å†…æ²»ç†</span> <span>â— å®¤å†…æ²»ç†</span> <span>â— æ£€æµ‹&é‡æˆ¿</span> <span>â— æœåŠ¡å’¨è¯¢</span> <span>â— é’¥åŒ™æ‰˜ç®¡</span>' }
            ],
            scrollTo: 0
        }
    },
    mounted: function() {
        var self = this;
        var scrollCacheFn = setInterval(function() {
            var scrollCache = 
                document.getElementsByClassName('pageTop')[0].offsetHeight + 
                document.getElementsByClassName('banner')[0].offsetHeight + 
                document.getElementsByClassName('showSpace')[0].offsetHeight + 
                document.getElementsByClassName('showSpace')[1].offsetHeight + 
                document.getElementsByClassName('showThree')[0].offsetTop - 
                window.innerHeight
            if(self.scrollTo === scrollCache) {
                clearInterval(scrollCacheFn)
            } else {
                self.scrollTo = scrollCache
            }
        }, 100)
        this.scrollCacheFn;
    },
}
</script>

