<template>
    <div :class="{ fourPoint: true, fourAnimate: scrollNumForShow > scrollTo }">
        <div v-for="fourPoint in fourPoints" :key="fourPoint.id">
            <div>
                <i class="iconfont" v-html="fourPoint.icon"></i>
                <img class="img-responsive" src="../../../static/img/title_2.png">
                <div>
                    <p>{{fourPoint.No}}</p>
                    <p v-html="fourPoint.word"></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        scrollNumForShow: {
            type: Number,
            required: true
        }
    },
    data: function() {
        return {
            fourPoints: [
                { id: 0, icon: '&#xe647;', No: '01', word: '<span>ç©ºæ°”å‡€åŒ–</span> <span>åˆ†è§£ç”²é†›ã€æ°¨å’Œè‹¯ç­‰æ±¡æŸ“ç‰©<br>æ°§åŒ–åŽ»é™¤æ°®æ°§åŒ–ç‰©ç­‰æœ‰å®³æœ‰æœºç‰©</span>' },
                { id: 1, icon: '&#xe649;', No: '02', word: '<span>æ€èŒæŠ‘èŒ</span> <span>æ€èŒåŠŸæ•ˆé«˜è¾¾99.96%ï¼ˆå¤§è‚ æ†èŒã€é»„è‰²è‘¡è„çƒèŒï¼‰<br>å®Œå…¨åŽ»é™¤å°˜èž¨ï¼ˆè¿‡æ•è‡´ç—…æºï¼‰</span>' },
                { id: 2, icon: '&#xe646;', No: '03', word: '<span>é™¤è‡­é™¤å‘³</span> <span>é™¤è‡­æ•ˆæžœæ˜Žæ˜¾æ˜¯é«˜æ€§èƒ½çº¤ç»´æ´»æ€§ç‚­çš„150å€<br>1cãŽ¡=500ä¸ªæ´»æ€§ç‚­å†°ç®±é™¤è‡­å‰‚</span>' },
                { id: 3, icon: '&#xe64c;', No: '04', word: '<span>é˜²éœ‰é˜²æ±¡</span> <span>é˜²æ­¢æ²¹æ±¡ã€ç°å°˜çš„äº§ç”Ÿ<br>é˜²æ­¢éœ‰èŒã€é»„ç¢±åŠé“é”ˆç­‰</span>' }
            ],
            scrollTo: 0
        }
    },
    mounted: function() {
        var pageIndex = parseInt(sessionStorage.getItem("pageIndex"))
        if(pageIndex == 2) {
            var self = this;
            var scrollCacheFn = setInterval(function() {
                var scrollCache = 
                    document.getElementsByClassName('pageTop')[0].offsetHeight + 
                    document.getElementsByClassName('banner')[0].offsetHeight + 
                    document.getElementsByClassName('showSpace')[0].offsetHeight + 
                    document.getElementsByClassName('showSpace')[1].offsetHeight + 
                    document.getElementsByClassName('showSpace')[2].offsetHeight + 
                    document.getElementsByClassName('fourPoint')[0].offsetTop - 
                    window.innerHeight
                if(self.scrollTo === scrollCache) {
                    clearInterval(scrollCacheFn)
                } else {
                    self.scrollTo = scrollCache
                }
            }, 1000)
            this.scrollCacheFn;
        }
    },
}
</script>

